.app {
  display: flex;
  padding-top: 40px;
  align-items: center;
  justify-content: center;
}

.container {
  position: relative;
  min-width: 1200px;
  min-height: 600px;
  padding: 40px;
  display: flex;
  position: relative;
  font-family: "Calibri", sans-serif;
  color:#1376D1;
  border: 2px solid #1376D1;
}

.title, .profile, .school, .back {
  position: relative;
  flex: 1;
  z-index: 10;
}

.title {
  width: 100%;
}

.school {
  font-size: 14px;
  text-align: right;
}

.pupil {
  padding-right: 10px;
  text-align: right;
}

#lottie {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
